button.signup {
    color: white;
    background: linear-gradient( 318deg, #0054d3, #589bff);
    border: none;
    width: 64%;
    height: 25px;
    border-radius: 11px;
    background-size: 400% 400%;
    -webkit-animation: signup_button_animation 3s ease-in-out infinite;
    animation: signup_button_animation 3s ease-in-out infinite;
    position: relative;
    z-index: 1;
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
    cursor: pointer;
}
button.signup:hover {
    -webkit-animation-play-state: running;
            animation-play-state: running;

}
@-webkit-keyframes signup_button_animation {
    0%{background-position:0% 20%}
    50%{background-position:100% 20%}
    100%{background-position:0% 20%}
}
@keyframes signup_button_animation {
    0%{background-position:0% 20%}
    50%{background-position:100% 20%}
    100%{background-position:0% 20%}
}
button.signin {
    background: #ffffff00;
    color: #0066FF;
    font-weight: 500;
    width: 47%;
    margin: 0px 8px 0px 0px;
    height: 25px;
    padding: 0px 6px 0px 6px;
    border-radius: 11px;
    border: 2px solid #0066FF;
    transition: 400ms;
    cursor: pointer;
}
button.signin:hover{
    background: linear-gradient( 318deg, #0054d3, #589bff);
    border: 2px solid #ffffff;
    color: #ffffff;
}
.footer {
