}
.footer_wrapper {
    display: flex;
    align-items: center;
    flex-direction: row;
    height: 100%;
    width: 100%;
    justify-content: space-between;
}
.copyright_contaner {
    flex: 2 1;
    background-color: white;
    height: 100%;
    border-radius: 15px;
    box-shadow: 0px 0px 20px rgb(0 0 0 / 6%);
    display: flex;
    align-items: center;
    justify-content: center;
}
.socialmedia_contaner {
    height: 100%;
    flex: 1 1;
