.contact {
    background: white;
    box-shadow: 0px 0px 20px rgb(0 0 0 / 6%);
    border-radius: 15px;
    height: 64%;
    overflow: hidden;
}
.conatact_wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 0px;
}
.contacts_container {
    overflow: scroll;
    height: 100%;
    margin: 0 20px 0 20px;
    overflow-x: hidden;
}
.contacts_container::-webkit-scrollbar{
    width: 7px;
}
.contacts_container::-webkit-scrollbar-track {
    background: #ffffff00;
  }
.contacts_container::-webkit-scrollbar-thumb {
    border-radius: 100px;
    background: #3866ff;
  }
.contacts_container::-webkit-scrollbar-thumb:hover {
    background: #0836ccc6;
}
h2.contact_header {
    color: #376af8;
    margin: 20px 0px 20px 20px;
}
.contact_info {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0 20px 20px 0;
}
.contact_img_status {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: relative;
}
.contact_status {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    position: absolute;
    top: -3px;
    right: -3px;
    border: solid 3px white;
}
.contact_status.unactive{
    background: #cfcfcf;
}
.contact_status.active{
    background: #10f92d;
}
img.contact_img {
    width: 70px;
    height: 70px;
    object-fit: cover;
    border-radius: 50%;
}
.contact_call_talk {
    width: 15%;
    color: #376af8;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.contact_talk,.contact_call{
    cursor: pointer;
}
.contact_talk:hover,.contact_call:hover{
    color: #376bf8c6;
}
.slideshow {
    width: 69%;
    height: 100%;
}
.slideshow_wrapper {
    width: 100%;
    height: 100%;
    position: relative;
}
.slide_controls {
    width: 17%;
    height: 5%;
    border-radius: 100px;
    background: linear-gradient( 
318deg, #0054d3, #589bff);
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    color: white;
    position: absolute;
    right: 0;
    margin: 20px;
    z-index: 1;
}
.dot {
    width: 15px;
    height: 15px;
    border: 2px solid white;
    border-radius: 10px;
    cursor: pointer;
}
.dot.active {
    background-color: white;
}
.slide_controls_arrows {
    background-color: white;
    color: #4b87ff;
    display: flex;
    border-radius: 43px;
    cursor: pointer;
    transition: 300ms;
}
.slide_controls_arrows:hover{
    background: #e9e9e9;
}
.slideitem {
    width: inherit;
    height: inherit;
    opacity: 0;
    position: absolute;
    transition: 500ms;
    transition-timing-function: ease-in-out;
}
.slideitem.active{
    opacity: 1;
}
.slide_items_contaner {
    width: inherit;
    height: inherit;
    display: contents;
    background: white;
}
img.slide_item_img {
    width: inherit;
    height: inherit;
    object-fit: cover;
    position: relative;
    z-index: -1;
}
.slideshow {
    width: auto;
    height: 100%;
    box-shadow: 0px 0px 20px rgb(0 0 0 / 6%);
    border-radius: 15px;
    overflow: hidden;
}
.slide_back_img {
    width: inherit;
    height: inherit;
    background: radial-gradient(129.83% 129.83% at 0% 100%, #000000 0%, rgba(0, 0, 0, 0) 83.85%);
}
.slide_item_info {
    position: absolute;
    bottom: 0%;
    width: 95%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 20px;
    flex-direction: column;
    justify-content: space-between;
    color: white;
}
h1.slide_item_titel {
    width: 59%;
}
h3.slide_item_desc {
    margin: 20px 0 0 0;
}
button.slide_item_button {
    width: 20%;
    height: 41px;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    background: linear-gradient( 
318deg, #0054d3, #589bff);
    color: white;
    transition: 0.5s;
    margin: 20px 0 0 0;
    background-size: 400% 400%;
    -webkit-animation: lide_item_button_animation 3s ease-in-out infinite;
    animation: lide_item_button_animation 3s ease-in-out infinite;
    position: relative;
    z-index: 1;
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
}
button.slide_item_button:hover {
    -webkit-animation-play-state: running;
            animation-play-state: running;
}
@-webkit-keyframes lide_item_button_animation {
    0%{background-position:0% 20%}
    50%{background-position:100% 20%}
    100%{background-position:0% 20%}
}
@keyframes lide_item_button_animation {
    0%{background-position:0% 20%}
    50%{background-position:100% 20%}
    100%{background-position:0% 20%}
}
